.text {

    position: absolute;
    width: 150px;
    height: 60px;
    left: 1000px;
    top: 10px;
    border-radius: 20px;
    background: #0066CC;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);

}

.text:after {
    width: 0;
    height: 0;
    border-top: 200px solid #f0ad4e;
    border-left: 200px solid transparent;
}

.name {
    position: absolute;
    width: 40px;
    height: 22px;
    left: 70px;
    top: 0px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    /* identical to box height */


    color: #FFFFFF;
}

.messageText {
    position: absolute;
    width: 40px;
    height: 22px;
    left: 20px;
    top: 40px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 50;
    font-size: 16px;
    line-height: 20px;
    /* identical to box height */


    color: #FFFFFF;


}

.time {
    position: absolute;
    width: 26px;
